.forecast {
  &__cards {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
    flex-wrap: wrap;
    & :hover {
      scale: 1.1;
    }
  }
  &__card {
    width: 130px;
    border: 1px solid black;
    padding: 5px;
    & :hover {
      scale: 1;
    }
  }
}
